html{
  padding:0px;
  margin: 0px;
  height: 100%;
}
body{
  height: 100%;
  background: black;
  padding:0px;
  margin: 0px;
}

.App {
  text-align: center;
  padding:0px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.board{

  padding-top: 10px;
}
table{
border-collapse: collapse;




}
table.table, table.table tr, table.table td{

  margin: auto;
}
table.table td {
  height: 70px;
  width: 70px;
  text-align: center;
  font-size: 32px;
  color: white;
}
table tr:nth-child(odd) td:nth-child(odd),
table tr:nth-child(even) td:nth-child(even) {
  background: #556B2F;
}
table tr:nth-child(even) td:nth-child(odd),
table tr:nth-child(odd) td:nth-child(even) {
  background: #918151;
}

.targeted{
  outline: 5px solid red;
  outline-offset: -5.3px;
}
.possibleMove{
  outline: 5px solid yellow;
  outline-offset: -5.3px;
}
.normal {

}
.piece{
  width: auto;
  height: 45px;
  padding-top: 10px;
}
@media all and (max-device-width: 667px) and (orientation: portrait){
  table{
    border-collapse: collapse;
  }
  .board{
    padding: 0px
  }
  td{
    width: 5px;
    height: 5px;
    text-align: center;
  }
}
@media all and (max-device-width: 667px) and (orientation: landscape){
  table{
    border-collapse: collapse;
  }
  .board{
    padding: 0px
  }
  table tr:nth-child(even) td:nth-child(odd),
  table tr:nth-child(odd) td:nth-child(even) {
    background: yellow;
  }
  td{
    width: calc(65vh/8);
    height: calc(65vh/8);
    text-align: center;
  }
}



